window.dom={
    //æ–°å¢žèŠ‚ç‚¹
    create(string){//ç”¨äºŽåˆ›å»ºå¸¦æœ‰å†…å®¹çš„å…ƒç´ 
        const container=document.createElement("template")//templateå¯ä»¥ç”¨äºŽæ’å…¥æ‰€æœ‰æ ‡ç­¾
        container.innerHTML=string.trim()//trimå¯ä»¥ç”¨äºŽå¿½ç•¥ç©ºæ ¼å’Œæ–‡æœ¬å…ƒç´ 
        return container.content.firstChild;
    },
   after(node,node2){
       node.parentNode.insertBefore(node2,node.nextsibling)//åœ¨èŠ‚ç‚¹åŽé¢æ·»åŠ ä¸€ä¸ªæ–°çš„èŠ‚ç‚¹ï¼Œåªèƒ½ç”¨åœ¨ä¸‹ä¸€ä¸ªèŠ‚ç‚¹å‰é¢æ·»åŠ ä¸€ä¸ªæ–°çš„èŠ‚ç‚¹ã€‚
   },
   before(node,node2){
       node.parentNode.insertBefore(node2,node)//åœ¨nodeå‰é¢æ–°å¢žä¸€ä¸ªèŠ‚ç‚¹
   },
    append(parent,node){
        parent.appendChild(node)//ç”¨äºŽæ–°å¢žä¸€ä¸ªå„¿å­
    },
    wrap(node,parent){
        dom.before(node,parent)//å…ˆæŠŠæ–°çš„èŠ‚ç‚¹æ”¾åœ¨èŠ‚ç‚¹çš„å‰é¢æˆ–è€…åŽé¢
        dom.append(parent,node)//å†æŠŠnodeæ”¾åˆ°parentçš„é‡Œé¢ï¼Œå®ƒå…ˆå‰çš„ä½ç½®ä¼šæ¶ˆå¤±
    },
    //åˆ é™¤
    remove(node){
        node.parentNode.removeChild(node)
        return node //å¯èƒ½è¿˜è¦ç”¨
    },
    empty(node){
        //ç®€å†™const childNodes=node.childNodes===const {childNodes}=node
        const array=[]
        let x=node.firstChild
        while(x){
            array.push(dom.remove(node.firstChild))
            x=node.firstChild
        }
        return array
    },
    //æ”¹å†™
    attr(node,name,value){//é‡è½½ï¼šæ ¹æ®å‚æ•°ä¸ªæ•°å†™ä¸åŒçš„ä»£ç ï¼Œå«é‡è½½
        if(arguments.length===3){
            node.setAttribute(name,value)
        }else if(arguments.length===2){
            return node.getAttribute(name)//è¦è¿”å›žä¸€ä¸ªå€¼
        }
    },
    text(node,string){//é€‚é…
        if(arguments.length===2){
            if('innerText'in node){
                node.innerText=string   //ie
             }else{
                 node.textContent=string  //ç«ç‹,chrome
             }   
        }else if(arguments.length===1){
             if('innerText'in node){
                 return node.innerText
             }else{
                 return node.textContent
             }
        }
    },
    html(node,string){
        if(arguments.length===2){//arugmentæ˜¯å‡½æ•°è°ƒç”¨æ—¶çš„å®žé™…å‚æ•°
            node.innerHTML=string
        }else if(arguments.length===1){
            return node.innerHTML
        }
    },
    style(node,name,value){
        if(arguments.length===3){
            //dom.style(div,'color','red')
            node.style[name]=value
        }else if(arguments.length===2){
            if(typeof name==='string'){
                //dom.style(div,'color')
                return node.style[name]
            }else if(name instanceof Object){
                //dom.style(div,{color:'red'})
                const object=name
                for(let key in object){
                    node.style[key]=object[key]
                }
            }
        }
    },
    class:{
        add(node,className){
            node.classList.add(className)
        },
        remove(node,className){
            node.classList.remove(className)
        },
        has(node,className){
            return node.classList.contains(className)
        }
    },
     on(node,eventName,fn){
         node.addEventListener(eventName,fn)
     },
     off(node,eventName,fn){
         node.removeEventListener(eventName,fn)
     },
     find(selector,scope){
         return (scope||document).querySelectorAll(selector)
     },
     parent(node){
         return node.parentNode
     },
     children(node){
         return node.children
     },
     siblings(node){
         return Array.from(node.parentNode.children).filter(n=>n!==node)
     },
     next(node){
         let x=node.nextSibling
         while(x&&x.nodeType===3){
             x=x.nextSibling
         }
         return x
     },
     previous(node){
         let x=node.previousSibling
         while(x&&x.nodeType===3){
            x=x.previousSibling
         }
         return x
     },
     each(nodeList,fn){
         for(let i=0;i<nodeList.length;i++){
             fn.call(null,nodeList[i])
         }
     },
     index(node){
         const list=dom.children(node.parentNode)
         let i
         for(i=0;i<list.length;i++){
             if(list[i]===node){
             break
            }
            }
            return i
          }
         

}
